import { motion, useInView, useMotionValue, useSpring, useTransform } from "framer-motion";
import { useRef, useState, useEffect } from "react";
import { isMobileDevice, prefersReducedMotion } from "../src/utils/deviceDetect";
import { useLanguage } from "../contexts/LanguageContext";
import { getTranslation } from "../translations";

export function InteractiveDemo() {
  const { language } = useLanguage();
  const t = getTranslation(language);
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, margin: "-100px" });
  const [isMobile, setIsMobile] = useState(false);
  const [reducedMotion, setReducedMotion] = useState(false);

  useEffect(() => {
    setIsMobile(isMobileDevice());
    setReducedMotion(prefersReducedMotion());
  }, []);

  const mouseX = useMotionValue(0);
  const mouseY = useMotionValue(0);

  const rotateX = useSpring(useTransform(mouseY, [-300, 300], [15, -15]), {
    stiffness: 100,
    damping: 30,
  });
  const rotateY = useSpring(useTransform(mouseX, [-300, 300], [-15, 15]), {
    stiffness: 100,
    damping: 30,
  });

  const animationDuration = isMobile || reducedMotion ? 0.3 : 0.6;

  const handleMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
    const rect = e.currentTarget.getBoundingClientRect();
    const centerX = rect.left + rect.width / 2;
    const centerY = rect.top + rect.height / 2;
    mouseX.set(e.clientX - centerX);
    mouseY.set(e.clientY - centerY);
  };

  const handleMouseLeave = () => {
    mouseX.set(0);
    mouseY.set(0);
  };

  return (
    <section ref={ref} className="py-32 relative overflow-hidden">
      <div className="container mx-auto px-6">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={isInView ? { opacity: 1, y: 0 } : {}}
          transition={{ duration: animationDuration }}
          className="text-center mb-20 will-change-transform"
        >
          <h2 className="text-5xl md:text-6xl font-bold mb-4">
            <span className="bg-gradient-to-r from-white to-cyan-400 bg-clip-text text-transparent">
              {t.interactiveDemo.title}
            </span>
          </h2>
          <p className="text-xl text-slate-400 max-w-2xl mx-auto">
            {t.interactiveDemo.description}
          </p>
        </motion.div>

        {/* 3D Interactive Image Display */}
        <div className="max-w-6xl mx-auto">
          <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            animate={isInView ? { opacity: 1, scale: 1 } : {}}
            transition={{ duration: animationDuration }}
            style={{
              perspective: 2000,
            }}
            onMouseMove={!isMobile && !reducedMotion ? handleMouseMove : undefined}
            onMouseLeave={!isMobile && !reducedMotion ? handleMouseLeave : undefined}
            className="relative"
          >
            <motion.div
              style={!isMobile && !reducedMotion ? {
                rotateX,
                rotateY,
                transformStyle: "preserve-3d",
              } : {}}
              className="relative"
            >
              {/* TV Screen Mockup */}
              <div className="relative aspect-video rounded-3xl overflow-hidden bg-slate-900 border-4 border-slate-700 shadow-2xl">
                {/* Bezel Effect */}
                <div className="absolute inset-0 border-8 border-black/50 rounded-3xl pointer-events-none z-10" />

                {/* Screen Content - Single Image */}
                <div className="relative h-full">
                  <img
                    src="/comparison-image.webp"
                    alt={t.interactiveDemo.imageAlt}
                    className="w-full h-full object-cover"
                    loading="lazy"
                    decoding="async"
                  />

                  {/* Hover Glow Effect */}
                  <motion.div
                    initial={{ opacity: 0 }}
                    whileHover={{ opacity: 1 }}
                    className="absolute inset-0 bg-gradient-to-br from-cyan-400/10 to-blue-600/10 blur-2xl pointer-events-none"
                  />
                </div>

                {/* Screen Glare Effect */}
                <div className="absolute inset-0 bg-gradient-to-br from-white/5 via-transparent to-transparent pointer-events-none" />
              </div>

              {/* TV Stand */}
              <div
                className="absolute -bottom-4 left-1/2 -translate-x-1/2 w-48 h-8 bg-gradient-to-b from-slate-700 to-slate-800 rounded-b-xl"
                style={{ transform: "translateZ(-50px) translateX(-50%)" }}
              />
            </motion.div>
          </motion.div>

          {/* Feature Highlights - Commented out per requirements */}
          {/* <motion.div
            initial={{ opacity: 0, y: 30 }}
            animate={isInView ? { opacity: 1, y: 0 } : {}}
            transition={{ duration: 0.6, delay: 0.4 }}
            className="grid grid-cols-3 gap-6 mt-16 max-w-3xl mx-auto"
          >
            {[
              { label: "3x Larger Text", value: "300%" },
              { label: "Remote Navigation", value: "100%" },
              { label: "Video Compatibility", value: "99.9%" },
            ].map((stat, index) => (
              <div key={index} className="text-center p-6 rounded-2xl bg-slate-900/50 border border-slate-700/50 backdrop-blur-sm">
                <div className="text-3xl font-bold text-cyan-400 mb-2">
                  {stat.value}
                </div>
                <div className="text-sm text-slate-400">{stat.label}</div>
              </div>
            ))}
          </motion.div> */}
        </div>
      </div>
    </section>
  );
}

export default InteractiveDemo;
